.App {
	max-width: 200px;
	margin: 200px 0 0 750px;
	display: flex;
	text-align: center;
}
/* https://get.wallhere.com/photo/1680x1050-px-cosmos-Earth-from-Moon-outer-space-universe-1675639.jpg */
/* https://webanetlabs.net/_ph/1/176719916.jpg */
body {
	background-image: url(https://s1.1zoom.ru/big0/677/Cats_Head_514965_1280x720.jpg);
	background-size: cover;
}
ul {
	padding: 0;
}
li {
	list-style: none;
	background-color: rgb(3, 194, 28);
	width: 200px;
	padding: 10px 10px;
	margin-top: 16px;
	display: flex;
	align-items: center;
	align-content: center;
	justify-content: space-between;
	border-radius: 20px;
	color: #fff;
	font-size: 18px;
	font-weight: 700;
	cursor: pointer;
	box-shadow: 3px 5px 3px #000;
	transition: 0.4s;
}
.btnLi {
	color: rgb(255, 4, 4);
	font-size: 15px;
	border-radius: 40%;
	border: none;
	cursor: pointer;
	transition: 0.5s;
	outline: none;
}
li:hover {
	transform: scale(1.1);
}
.btnLi:hover {
	transform: scale(1.2);
	color: rgb(255, 0, 0);
	font-weight: 700;
}
.btnPanel {
	padding: 5px 9px;
	margin-left: 10px;
	text-align: center;
	background-color: rgb(0, 140, 255);
	color: #fff;
	font-size: 14px;
	border: none;
	border-radius: 5px;
	font-weight: 600;
	transition: 0.4s;
	outline: none;
}
.btnPanel:hover {
	border: 1px solid black;
	transform: scale(1.1);
}
.input {
	background-color: rgb(255, 255, 255);
	padding: 5px 11px;
	border-radius: 30px;
	border: none;
	outline: none;
}
.input:focus {
	background-color: rgb(228, 228, 228);
}
.btn {
	text-align: center;
	border-radius: 100%;
	border: none;
	margin-left: 6px;
	font-weight: 700;
	font-size: 18px;
	color: rgb(51, 0, 235);
}
.active {
	padding: 5px 9px;
	margin-left: 10px;
	text-align: center;
	color: #fff;
	font-size: 14px;
	border: none;
	border-radius: 5px;
	font-weight: 600;
	transition: 0.4s;
	outline: none;
	background-color: rgb(201, 0, 151);
}
.done {
	opacity: 0.6;

	transition: 0.5s;
	background-color: rgb(11, 59, 150);
}
.inpT {
	border-radius: 100%;
	background-color: red;
	color:red
}
@media screen and (max-width: 440px) {
	.App {
		margin: 0 auto;
		margin-top: 150px;
	}
	body {
		background-image: url(https://s1.1zoom.ru/big0/677/Cats_Head_514965_1280x720.jpg);
		background-position: center center;
		background-size: cover;
		background-repeat: no-repeat;
		height: 700px;
	}
}
